<template>
    <element-container @resized="containerWidth = $event.width">
    <div class="statamic-resrv-availability">
        <div class="w-full h-full">
            <FullCalendar
                ref="calendar"
                :options="calendarOptions"
            />
        </div>
    </div>
    </element-container>

</template>

<script>
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import dayjs from 'dayjs'

export default {

    mixins: [Fieldtype],

    data() {

        return {
            containerWidth: null,
            calendarOptions: {
                plugins: [ dayGridPlugin, interactionPlugin ],
                selectable: true,
                initialView: 'dayGridMonth',
                select: this.handleSelect,
                dayCellContent: this.renderDay,
            },
            availability: {
                '2021-04-24': {
                    available: 2,
                    price: 220
                },
                '2021-04-25': {
                    available: 2,
                    price: 220
                },
                '2021-04-26': {
                    available: 1,
                    price: 200
                },
                '2021-04-27': {
                    available: 1,
                    price: 200
                },
                '2021-04-28': {
                    available: 0,
                    price: 180
                },

            }
        }
    },

    components: {
        FullCalendar
    },

    computed: {
      
    },

    watch: {

    },

    methods: {
        handleSelect(date) {
            console.log(date)
        },
        renderDay(arg) {
            let arrayOfDomNodes = []
            let day = dayjs(arg.date).format('YYYY-MM-DD')
            const defaultClasses = ['p-1', 'text-xs', 'text-white', 'bg-green-500'];
            
            // Day label
            let dayLabel = document.createElement('div')
            dayLabel.classList.add('mt-1', 'mb-1')
            dayLabel.innerHTML = arg.dayNumberText
            arrayOfDomNodes.push(dayLabel)

            // Availability & price
            let avail = document.createElement('div')          
            let price = document.createElement('div')          
            if (day in this.availability) {
                avail.classList.add(...defaultClasses, 'bg-green-500')
                price.classList.add(...defaultClasses, 'bg-gray-500')
                avail.innerHTML = '# '+this.availability[day].available
                price.innerHTML = '€ '+this.availability[day].price
            } else {
                avail.classList.add(...defaultClasses, 'bg-red-500')
                price.classList.add(...defaultClasses, 'bg-gray-500')
                avail.innerHTML = '# 0'
                price.innerHTML = '€ 0'
            }
            arrayOfDomNodes.push(avail)
            arrayOfDomNodes.push(price)

            return { domNodes: arrayOfDomNodes }
        }
    }
}
</script>
